.header{
	background-color: #ccc;
	position: relative;
	width: 100%;
	border-radius: 60px;
}

h1{
	text-align: center;
	font-size: 90px;
	font-weight: bold;
	padding: 90px 0 20px 0; 
}

h2{
	text-align: center;
	font-size: 25px;
	padding-bottom: 40px;
}
.card {
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 200px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card > .img-container > img {
  width: 100%;
  height: 100%;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card:hover {
  -webkit-animation: pulse 0.5s;
  animation: pulse 0.5s;
}

.footer{
	background: #052249;
	width: 100%;
	position: relative;
	height: 60px;
	border-top-left-radius: 60px;
	border-top-right-radius: 60px;
	
	
}

.footer p{
	color: white;
	text-align: center;
	padding: 10px 0;
	font-size: 15px;
	text-align: left;
	padding-left: 40px;
	
}
